.model-exsamples {
    padding-top: 40px;
    padding-bottom: 0px;
    @include desktop {
        padding-top: 100px;
        padding-bottom: 0px;
    }
}

.model-exsamples__container {
    
}

.model-exsamples__title {
    padding-bottom: 16px;
    @include desktop {
        padding-left: 117px;
        padding-bottom: 24px;
    }
}

.card-list {
    display: flex;
    @media screen and (max-width: 767px) {
        flex-wrap: wrap;
        gap: 20px 16px;
    }

    @include tablet {
        gap: 32px;
    }
}

.card-list__item {
    justify-content: center;
    width: calc((100% - 20px) / 2);
    height: 158px;
    background-color: #FFFFFF;
    opacity: 0.8;
    box-shadow: inset 1px 1px 16px rgba(203, 203, 203, 0.8), inset -1px -1px 16px #FFFFFF;
    border-radius: 50px 0px;
    
    @include tablet {
        width: calc((100% - 96px) / 4);
        height: 182px;
    }

    @include desktop {
        width: calc((100% - 96px) / 4);
        height: 382px;        
    }
}

.card-list__wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
}

.card-list__first {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 89.83px;
    height: 74.37px;
    margin-bottom: 17.06px;
    @include tablet {
        width: 105.04px;
        height: 85.5px;
    }

    @include desktop {
        width: 240px;
        height: 240px;
    }
}

.card-list__second {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 100.88px;
    height: 63.62px;
    margin-bottom: 16.67px;
    @include tablet {
        width: 117.95px;
        height: 73.29px;
    }

    @include desktop {
        width: 240px;
        height: 240px;
    }
}

.card-list__third {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 43.16px;
    height: 70.52px;
    margin-bottom: 16.67px;
    @include tablet {
        width: 50.46px;
        height: 81.08px;
    }

    @include desktop {
        width: 149.42px;
        height: 240px;
    }
}

.card-list__fourth {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 83.72px;
    height: 85.36px;
    margin-bottom: 9.25px;
    @include tablet {
        width: 97.89px;
        height: 98.14px;
    }

    @include desktop {
        width: 240px;
        height: 240px;
    }
}

.card-list__title {
    display: block;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: calc(18 / 14);
    text-align: center;
    color: var(--second-title-color);
    @include desktop {
        font-weight: 400;
        font-size: 18px;
        line-height: calc(23 / 18); 
    }
}

